<script>
export default {
  created() {
    this.setHeaders();
    this.setHeadersStock_movement();
    this.setHeadersInventory();
  },
  data: () => ({
    headers: [],
    events: [],
    items: [],
    meta: {},
    supply: false,
    itemPerPage: 10,
    //itemPerPages: 3,
    itemPerPageOptions: [5, 10, 30],
    urlItems: "",
    urlItemsDetails: "",
    urlDeleteItems: "",
    sortBy: "id",
    sortDesc: true,
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    loadingDetails: {
      list: false,
      refresh: false,
      filter: false
    },
    dialogDelete: {
      show: false
    },
    pagination: {},
    selectedItem: false,
    _search: {},
    _sorts: {}
  }),
  watch: {
    pagination: {
      handler() {
        this.getItems();
      },
      deep: true
    }
  },
  methods: {
    setHeaders() {
      this.headers = [];
    },
    setHeadersStock_movement() {
      this.headersStock_movement = [];
    },
    setHeadersInventory() {
      this.headersInventory = [];
    },
    refreshItems() {
      let self = this;
      this.loading.refresh = true;
      this.getItems()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loading.refresh = false;
        });
    },
    refreshItemsDetails() {
      let self = this;
      this.loadingDetails.refresh = true;
      this.getItemsDetails()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loadingDetails.refresh = false;
        });
    },
    refreshItemsPlus() {
      let self = this;
      this.loading.refresh = true;
      this.getItemsPlus()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loading.refresh = false;
        });
    },
    getItemsDetails() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingDetails.list = true;
        self.$store
          .dispatch("request", {
            url: "details_supplies" + "?supplies=" + this.form.id_supplies,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.items = data.content;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loadingDetails.list = false;
            resolve();
          });
      });
    },
    getItemsPlus() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loading.list = true;
        self.$store
          .dispatch("request", {
            url: self.urlItems + "?id=" + this.$route.params.id,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.items = data;
            self.meta = { totalElements: data.totalElements };
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loading.list = false;
            resolve();
          });
      });
    },
    getItems() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loading.list = true;
        self.$store
          .dispatch("request", {
            url: self.urlItems,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.items = data.content;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data.content[0]);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loading.list = false;
            resolve();
          });
      });
    },
    deleteItem(item) {
      this.urlDeleteItems = this.urlItems + "/" + item.id;
      this.dialogDelete.show = true;
    },
    getSorts(sortBy, sortDesc) {
      let sorts = {};
      if (this._.has(sortBy, "length") && this._.has(sortDesc, "length")) {
        if (sortBy.length > 0 && sortDesc.length > 0) {
          sortBy.forEach((s, k) => {
            sorts["_sorts[" + s + "]"] = sortDesc[k] ? "DESC" : "ASC";
          });
        }
      }
      return sorts;
    }
  }
};
</script>
